import requests from './request';

const sendRequest = (url, method, params = {}) => {
  return requests({
    url,
    method,
    params,
  });
};

export const getFirmware = () => {
  return sendRequest('/firmware', 'get');
};

export const getFirmwareProduct = () => {
  return sendRequest('/firmware/product', 'get');
};

export const getFlashListiPhone = (params) => {
  return sendRequest('/flashList/iPhone', 'get', params);
};
// ipad
export const getFlashListiPad = (params) => {
  return sendRequest('/flashList/iPad', 'get', params);
};
// ipod
export const getFlashListiPod = (params) => {
  return sendRequest('/flashList/iPod', 'get', params);
};
// Mac
export const getFlashListMac = (params) => {
  return sendRequest('/flashList/Mac', 'get', params);
};
// AppleTV
export const getFlashListAppleTV = (params) => {
  return sendRequest('/flashList/AppleTV', 'get', params);
};
// Watch
export const getFlashListWatch = (params) => {
  return sendRequest('/flashList/Watch', 'get', params);
};
// HomePod
export const getFlashListHomePod = (params) => {
  return sendRequest('/flashList/HomePod', 'get', params);
};
// HomePod
export const getFlashListVision = (params) => {
  return sendRequest('/flashList/Vision', 'get', params);
};
// 固件
export const getinfoDataList = (params) => {
  return sendRequest('/infoDataList/infoData', 'get', params);
};





